<template>
  <div></div>
</template>
<script type="text/ecmascript-6">
import {kpiWeChatOAuthCallBack} from 'api'
// 微信自动登录CALLBACK页面
export default {
  methods: {
    _authAPi() {
      let params = {
        Code: this.getUrlKey('code')
      }
      // console.log('code: ', params)
      if (!params.Code) {
        this.$toast(this.$t('TM.TheCodeValueIsEmpty'))
        return
      }
      kpiWeChatOAuthCallBack(params).then((res) => {
        console.log('自动登录: ', decodeURIComponent(res.ResultData))
        let url = ''
        if (
          window.localStorage.authCallbackPath === '/planeTicket/orderDetails' ||
          window.localStorage.authCallbackPath === '/intlPlaneTicket/orderDetails' ||
          window.localStorage.authCallbackPath === '/hotel/orderDetails' ||
          window.localStorage.authCallbackPath === '/inter-hotel/orderDetails' ||
          window.localStorage.authCallbackPath === '/train/orderDetail'
        ) {
          url = `${window.location.protocol}//${window.location.host}/#${window.localStorage.authCallbackFullPath}`
        } else {
          url = `${window.location.protocol}//${window.location.host}/#${decodeURIComponent(res.ResultData)}`
        }
        console.log('url: ', url)
        window.location.href = url
        // this.$router.replace(decodeURIComponent(res.ResultData))
      })
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm._authAPi()
    })
  }
}
</script>



